@use '@mview/scss/utilities/functions' as *;
@use '@mview/scss/utilities/mixins' as *;
@use 'sass:color';

.add-case-wrapper {
  padding: 24px;

  span {
    font-size: 16px;
    line-height: 20px;
    color: pick-color('primary', 'charcoal');
  }

  .case-header {
    svg {
      fill: pick-color('primary', 'charcoal');
    }

    span {
      font-size: 18px;
      font-weight: 500;
    }
  }

  .case-form {
    display: flex;
    align-items: center;
    gap: 16px;
    margin-top: 24px;

    .search-input-container {
      display: flex;
      justify-content: space-between;
      align-items: center;
      background-color: white;
      border: 1px solid pick-color('primary', 'fog');
      width: 260px;
      @include border-radius(8px);

      .search-input {
        padding: 8px 16px;
        border: none;
        background-color: transparent;
        font-size: 13px;
      }

      svg {
        fill: pick-color('primary', 'charcoal');
        width: 20px;
        height: 20px;
        margin-right: 16px;
      }
    }
  }

  .case-list {
    margin-top: 16px;
    border: 2px solid pick-color('primary', 'fog');
    padding: 16px;
    border-radius: 6px;

    li {
      position: relative;
      display: flex;
      align-items: center;
      cursor: pointer;
      color: pick-color('primary', 'charcoal');
      line-height: 20px;
      font-size: 14px;
      white-space: nowrap;
      gap: 10px;

      &:not(:last-child) {
        margin-bottom: 16px;
      }

      .check {
        display: flex;
        flex-shrink: 0;
        border: 1px solid pick-color('primary', 'ash');
        width: 16px;
        height: 16px;
        border-radius: 3px;
      }

      span {
        font-size: 14px;
      }

      &.selected {
        .check {
          background-color: pick-color('primary', 'web');
          position: relative;
          border: none;

          &::before {
            font-family: 'Material Icons';
            content: 'done';
            position: absolute;
            top: 50%;
            left: 50%;
            color: white;
            transform: translate(-50%, -50%);
            border-radius: inherit;
          }
        }
      }
    }
  }
}
